/*只适用于移动端*/
*{ padding: 0; margin: 0; border: 0; font-style: normal;
}
html, body{
	font-size: 50px;
	background-color: #fff;
	color: #151515;
	/* 禁用系统长按菜单 */
	-webkit-touch-callout: none;
	/* 禁用字体大小自动调整 */
	-webkit-text-size-adjust: none !important;
    -moz-text-size-adjust: none !important;
     -ms-text-size-adjust: none !important;
	     text-size-adjust: none !important;
	/* 去掉点击高亮 */
	-webkit-tap-highlight-color:rgba(0, 0, 0, 0);
	/* 禁止选择内容 */
	-webkit-user-select: none;
	font-family: "Helvetica Neue", Helvetica, sans-serif;
}
li{ list-style: none;
}
a{ text-decoration: none; color: #151515; background-color: transparent;
}
img{ display: block; width: 100%;
}
h1,h2,h3,h4,h5,h6,i,b,i{ font-weight: 400; font-style: normal;
}
input, textarea{ outline: none; background-color: rgba(0, 0, 0, 0); -webkit-user-select: auto; border: none;
}
input::-webkit-input-placeholder { color: #999;
}
textarea { resize: none; display: block;
}
textarea::-webkit-input-placeholder { color: #999;
}
/*定义rem*/
@media only screen and (min-width: 320px) {
html { font-size: 42.6667px !important;
}
}
@media only screen and (min-width: 360px) {
html { font-size: 48px !important;
}
}
@media only screen and (min-width: 375px) {
html { font-size: 50px !important;
}
}
@media only screen and (min-width: 400px) {
html { font-size: 53.33333333px !important;
}
}
@media only screen and (min-width: 414px) {
html { font-size: 55.2px !important;
}
}
@media only screen and (min-width: 480px) {
html { font-size: 64px !important;
}
}
[data-active]:active{ background-color: #eee;
}
[data-opacity]:active{ opacity: 0.8;
}
[data-spread]{ position: relative; overflow: hidden;
}
[data-spread]::before{ position: absolute; content: ''; background-color: rgba(255,255,255,0.1); width: 0px; height: 0px; top: 50%; left: 50%; border-radius: 50%; -webkit-transform: translate(-50%,-50%); transform: translate(-50%,-50%);
}
[data-spread]:active::before{ width: 80px; height: 80px; -webkit-transition: .2s all; transition: .2s all;
}
/*复选框*/
.checked_btn{ width: .4rem; height: .4rem; position: relative; border: solid 1px #e3b770; -webkit-transition: 0.2s all; transition: 0.2s all;
}
.checked_btn::before{ position: absolute; content: ''; -webkit-transition: 0.2s all; transition: 0.2s all;
}
.checked_btn::before{ width: 30%; height: 50%; background-color: transparent; left: 35%; top: 13%; -webkit-transform: rotate(45deg) scale(0,0); transform: rotate(45deg) scale(0,0);
}
.check_on{ background-color: #e3b770; border-color: #e3b770;
}
.check_on::before{ -webkit-transform: rotate(45deg) scale(1,1); transform: rotate(45deg) scale(1,1); border: solid 1px #fff; border-top: none; border-left: none;
}
/* 开关 */
.switch_btn{ width: .7rem; height: .26rem; background-color: #ccc; position: relative; border-radius: .13rem;
}
.switch_btn::before{ position: absolute; content: ''; -webkit-transition: 0.3s all; transition: 0.3s all; width: .4rem; height: .4rem; background-color: #fff; border-radius: 50%; left: 0; top: -0.075rem; -webkit-transform: translateX(0rem); transform: translateX(0rem); -webkit-box-shadow: 0px 1px 10px 0 rgba(0,0,0,0.2); box-shadow: 0px 1px 10px 0 rgba(0,0,0,0.2)
}
.switch_on{ background-color: #f1dbb8;
}
.switch_on::before{ background-color: #e3b770;-webkit-transform: translateX(0.3rem); transform: translateX(0.3rem);
}
